"use client";

import { useDeleteJob, useJobs } from "@/app/customHooks/useJobs";
import React from "react";


export default function JobsList() {
  const { data: jobs, isLoading, error } = useJobs();
  const { mutate: deleteJob } = useDeleteJob();

  if (isLoading) return <p>Loading jobs...</p>;
  if (error) return <p>Failed to load jobs.</p>;

  return (
    <div className="bg-white rounded-lg shadow p-6">
      <h2 className="text-xl font-semibold mb-4">Jobs List</h2>

      <table className="w-full text-left border-collapse">
        <thead>
          <tr>
            <th className="border-b p-2">Title</th>
            <th className="border-b p-2">Company</th>
            <th className="border-b p-2">Location</th>
            <th className="border-b p-2">Description</th>
            <th className="border-b p-2">Actions</th>
          </tr>
        </thead>
        <tbody>
          {jobs?.map((job) => (
            <tr key={job.id} className="hover:bg-gray-50">
              <td className="p-2">{job.title}</td>
              <td className="p-2">{job.company}</td>
              <td className="p-2">{job.location}</td>
              <td className="p-2">{job.description}</td>
              <td className="p-2">
                <button
                  onClick={() => deleteJob(job.id)}
                  className="text-red-500 hover:text-red-700 font-medium"
                >
                  Delete
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
}
